.main-content-wrapper {
  background-color: #000000;
}
.main-content-wrapper__image {
  width: 100%;
  height: 100%;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  background-color: #000000;
  opacity: 0.6;

}
.main-content-wrapper__title {
  color: #ffffff;
  font-weight: 300;
  font-size: 45px;
  text-align: center;
  font-weight: 600;
}
.main-content-wrapper__logo-background {
  position: absolute;
  background-color: #ffffff;
  border-radius: 50%;
  width: 110%;
  height: 110%;
  box-shadow: 0 0 0 8px #ffffff;
}
.main-content-wrapper__logo-container{
  width: 200px;
  height: 200px;
  margin: 0 auto;
  position: relative;
  right: 13px;
  margin-top: 30px;
}
.main-content-wrapper__title {
  text-shadow: 1px 1px 3px #000000;
  line-height: 50px;
  font-size: 55px;
}
.main-content-wrapper__title-transfer {
  margin-bottom: 0;
  font-size: 40px;
}
.main-content-wrapper__title-description {
  position: relative;
  bottom: 13px;
  font-size: 50px;
}
.main-content-wrapper__slogan {
  position: relative;
}
.main-content-wrapper__slogan-list {
  position: absolute;
  left: 50%;
  top:30%;
  transform: translate(-50%,-50%);
  list-style-type: none;
  padding: 0;
  margin: 0;
}
