.main-content-wrapper__slogan-description {
  color:#ffffff;
  font-size: 25px;
  font-weight: 300;
  text-align: center;
  white-space: nowrap;
  text-align: start;
}
.main-content-wrapper__russia {
  position: absolute;
  top:-33%;
  left: 47%;
  transform: translateX(-50%);
}
.main-content-wrapper__carousel {

  padding: 40px 0;
}
.main-content-wrapper__russia{
  margin: 0 auto;
  display: block;
}
