.main-header {

}
.main-header__wrapper-mobile {
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
}
.main-header__title {
  color:#000;
  font-size: 10px;
  text-align: center;
  position: relative;
  line-height: 22px;
  margin-right: auto;
  margin-left: 15px;
  top: 5px;
}
.main-header__title::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background-color: #000;
  left: 0;

}
.main-header__description {
  color:#000;
  font-size: 16px;
  text-align: center;
  font-weight: 600;
}
.main-header__main-image {

}
.main-header__main-logo {

}

.burger{
  position: relative;
  width: 50px;
  height: 50px;
  right: 15px;
  top: 7px;
  cursor: pointer;
  padding-bottom: 10px;
}
.burger span, .burger span:before, .burger span:after{
  width: 37px;
  position: absolute;
  height: 2px;
  background: #000;
  display: block;
  transition: .3s;
  border-radius: 5px;
}
.burger span:before{
  content: "";
  top: -15px;
  transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
.burger span{
  top: 15px;
}
.burger span:after{
  content: "";
  bottom: -15px;
  transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
.burger span.active:before{
  content: "";
  top: 0px;
  transform: rotate(45deg);
  transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.burger span.active:after{
  content: "";
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.burger span.active{
  background: rgba(255, 255, 255, 0);
}
@media(min-width: $mobile-m){
  .main-header__title {
    font-size: 14px;
  }
}
@media(min-width: $mobile-l){
  .main-header__title {
    font-size: 16px;
  }
}
